<template>
  <div class="share-anonymous-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        :pullup=true
        @pullUpLoad="loadMore"
        class="scroll-view"
        :probeType="3"
        :listenScroll="true">
        <div class="container"  v-show="!isInit">
          <div v-if="isDelete" class="header-delete">抱歉, 该吐槽信息已被作者删除~</div>
          <div v-if="!isDelete" class="header">
            <div class="owner">{{detail.name}}</div>
            <div class="msg" v-if="detail.content">{{detail.content}}</div>
            <div class="imgs" v-if="detail.image && detail.image.length > 0">
              <img v-for="(img, index) in detail.image"  
                :src="imgUrl + img"  
                :class="detail.image.length > 1 ? 'img' : 'onlyimg'" 
                :key="index" alt="">
            </div>
            <div class="msgs clearfix">
                <span class="time">{{detail.createtime}}</span>
                <div class="comment" @click="goWapDetail">
                  <span class="bgspan"></span>
                  <span>{{detail.commentcount}}</span>
                </div>
                <div class="share" @click="goWapDetail">
                  <span class="bgspan"></span>
                  <span>{{detail.sharecount}}</span>
                </div>
                <div class="loveno" @click="goWapDetail">
                  <span class="bgspan"></span>
                  <span>{{detail.unlikecount}}</span>
                </div>
                <div class="love" @click="goWapDetail">
                  <span class="bgspan"></span>
                  <span>{{detail.likecount}}</span>
                </div>
            </div>
          </div>

          <div class="footer">
            <div class="title">评论</div>
            <ul class="ul" v-if="list.length > 0 && !isDelete">
              <li class="li" v-for="(item, index) in list" :key="index">
                <img class="logo" :src="imgUrl + item.logo" alt="">
                <div class="detail">
                  <div class="user">
                    <span>{{item.name}}</span>
                    <div class="love" @click="goWapDetail">
                      <!-- <img class="loveimg" src="./icon_zan_u@2x.png" alt=""> -->
                      <span class="loveimg"></span>
                      <span>{{item.likecount}}</span>
                    </div>
                  </div> 
                  <div class="comment">
                    <span class="reply" v-if="item.parentName">回复{{item.parentName}}：</span> {{item.comment}}
                  </div>
                </div>
              </li>
            </ul>
            <div class="nocomment" v-if="list.length == 0 || isDelete">暂无评论</div>
          </div>
        </div>
      </pem-scroll-view>
      <div class="footer-open" @click="goDownload">
        <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
        <h4>项目工场</h4>
        <p>找项目、找投资就到项目工场</p>
        <div class="btn">打开</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      id: '',
      isInit: true,
      isDelete: false,
      imgUrl: ImgPath,
      detail: {},
      currentPage: 1,
      totalPage: 1,
      pageSize: '10',
      list: [],
      shareTitle: '快看! 竟然还有这种事, 有图有真相啊',
      shareDesc: '这条吐槽信息有点意思!',
      shareLogo: 'http://m.pemarket.com.cn/static/logo.png'
    }
  },
  components: {
    PemScrollView
  },
  computed: mapState({
    auth: state => state.auth
  }),
  created () {
    this._setTitle('投融资信息对接服务平台')
    this._initWXShare()
    this._initData()
  },
  methods: {
    goDownload () {
      if (device.isPC()) {
        if (device.isIOS()) {
          window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
        } else {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
        }
        return false
      }
      this.$router.push('/download')
    },
    goWapDetail () {
      if (device.isPC()) {
        if (device.isIOS()) {
          window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
        } else {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
        }
        return false
      }
      if (this.auth && this.auth.access_token) {
        this.$router.push('/index')
        this.$router.push(`/anonymous-new-detail/${this.id}`)
      } else {
        this.$router.push('/index')
        this.$router.push(`/login?go=anonymousdetail&id=${this.id}`)
      }
    },
    loadMore () {
      console.log('加载更多')
      if (this.currentPage >= this.totalPage) return
      this.currentPage = this.currentPage * 1 + 1
      let options = {
        anonymous_id: this.$route.query.id,
        currentPage: this.currentPage,
        pageSize: '10'
      }
      this._getAnonymousAppShareCommentList(options)
    },
    _initData () {
      this.id = this.$route.query.id
      console.log('renyun', this.$route)
      let option1 = {id: this.id}
      if (this.$route.query.fapp) option1['shareid'] = this.$route.query.fapp
      let option2 = {
        anonymous_id: this.id,
        currentPage: '1',
        pageSize: '10'
      }
      this._getAnonymousAppShareDetail(option1)
      this._getAnonymousAppShareCommentList(option2)
    },
    _getAnonymousAppShareDetail (options) {
      MiniSiteApi.getAnonymousAppShareDetail(options).then(res => {
        this.isInit = false
        if (+res.resultcode === 1) {
          this.detail = res || {}
          if (this.detail.content) this.shareTitle = this.detail.content
          if (this.detail.image && this.detail.image.length > 0) {
            this.shareLogo = this.imgUrl + this.detail.image[0]
          }
          this._initWXShare()
        } else if (+res.resultcode === 2) {
          this.isDelete = true // 删除或者禁用
        }
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    },
    _getAnonymousAppShareCommentList (options) {
      MiniSiteApi.getAnonymousAppShareCommentList(options).then(res => {
        this.isInit = false
        this.list = res && res.resultList && res.resultList.length > 0 ? res.resultList : []
        let mostLoveComment = ''
        if (this.list.length > 0) {
          let mostLoveIndex = this.list[0]['likecount'] || 0
          mostLoveComment = `热评: ${(this.list[0]['comment'].trim() || '')}`
          for (let i = 0, len = this.list.length; i < len; i++) {
            if (this.list[i]['likecount'] > mostLoveIndex) {
              mostLoveIndex = this.list[i]['likecount']
              mostLoveComment = `热评: ${(this.list[i]['comment'].trim() || '')}`
            }
          }
        }
        if (mostLoveComment) this.shareDesc = mostLoveComment
        this._initWXShare()
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    },
    _initWXShare () {
      this.wxShare({
        title: this.shareTitle,
        content: this.shareDesc,
        imgUrl: this.shareLogo
      })
    },
    _setTitle (title) {
      setTitle(title)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .share-anonymous-detail
    min-width: 300px
  .page-panel
    top: 0
    background-color: #f6f6f6
  .footer-open
      position: fixed
      left: 0
      bottom: 0
      right: 0
      height: 1.2rem
      border-top: 0.5px solid #f3f3f3
      background: #ffffff
      .logo
        position: absolute 
        left: 0.46rem
        top: 0.2rem
        height: 0.8rem
        width: 0.8rem
        border-radius: 0.08rem;
      h4
        position: absolute 
        left: 1.42rem
        top: 0.16rem
        font-family: PingFangSC-Medium
        font-size: 0.32rem
        font-weight: bold
        color: #41403E
        line-height: 0.44rem
      p
        position: absolute 
        left: 1.42rem
        bottom: 0.2rem
        font-family: PingFangSC-Regular
        font-size: 0.26rem
        color: #8B8B8B
        line-height: 0.36rem
      .btn
        position: absolute
        top: 0.32rem
        right: 0.22rem
        width: 1.56rem
        height: 0.6rem
        line-height: 0.6rem
        background: #FFC131
        border-radius: 0.04rem
        font-family: PingFangSC-Regular
        font-size: 0.3rem
        color: #41403E
        letter-spacing: -0.3px
        text-align: center
  .container
    padding-bottom:50px
    .header-delete
      text-align: center;
      background-color: #ffffff
      color: #a7abb2;
      font-size: 15px
      padding: 120px 10px 
    .header
      padding: 0 15px
      background-color: #ffffff
      .owner
        padding: 14px 0
        font-size: 15px
        color: #A7ABB2
      .msg
        padding-bottom: 18px
        font-size: 16px
        color: #2B2B2B
        line-height: 22px
      .imgs
        padding-bottom: 20px
        overflow: hidden
        .img
          float: left
          width: 100px
          height: 100px
          margin-right: 10px
          margin-bottom: 10px
        .onlyimg
          max-height: 200px
      .msgs
        color: #787F8B
        padding-bottom: 20px
        .time
          float: left
          font-size: 12px
          // height: 18px 
          // line-height: 18px 
        .comment, .share, .loveno, .love
          float: right
          display: inline-block
          // height: 18px 
          // line-height: 18px 
          font-size: 11px
          span
            display: inline-block
            vertical-align: middle 
          .bgspan
            width: 13px
            background-size: 100% 
            background-repeat: no-repeat
        .share, .loveno, .love
          margin-right:  19px
        .comment .bgspan
          bg-image('icon_pinglun')
          height: 11px
        .share .bgspan
          bg-image('icon_zhuanfa')
          height: 13px
        .loveno .bgspan
          bg-image('icon_cai_u')
          height: 13px
        .love .bgspan
          bg-image('icon_zan')
          height: 13px
    .footer
      padding: 0 15px
      .title
        padding: 14px 0 6px 0
        font-size: 14px
        font-weight: bold
        color: #41403E
      .nocomment
        text-align: center
        font-size: 12px
        color: #9A9A9A
        padding: 100px 0
      .li
        position: relative
        border-bottom: 0.5px solid #EFEFEF
        padding: 10px 0 10px 47px
        &:last-of-type
          border-bottom: none
        .logo
          position: absolute
          left: 0
          top: 10px
          width: 35px
          height: 35px
          border-radius: 100%
        .detail
          .user
            height: 18px
            line-height: 18px
            font-size: 13px
            color: #A1A1A1
            position: relative
            .love
              display: inline-block
              position: absolute
              height: 18px
              line-height: 18px
              right: 0
              font-size: 11px 
              .loveimg
                width: 13px
                height: 13px
                bg-image('icon_pingl_zan')
                background-size: 100% 
                background-repeat: no-repeat
              span
                vertical-align: middle
                display: inline-block 
          .comment
            padding-top: 6px
            font-size: 14px
            line-height: 20px 
            color: #353535
            .reply
              color: #A1A1A1
</style>



